			<view class="flex aic ju_b  pb12 pt12 b-b-1">
				<view class="flex aic">
					<text class="fs15 pl5">注册时间</text>
				</view>
				<view class="" style="width: 220rpx;">
					<picker mode="date" :value="date" :start="startDate" :end="endDate" @change="bindDateChange">
						<text class="uni-input fs14 cb">{{date}}</text>
					</picker>
				</view>
				<view class=" icon-arrow iconfont fs11 cb pl5 "></view>
			</view>
			<wangding-pickerAddress class="  " @change="change">
				<view class="flex aic ju_b  pb10 pt10">
					<view class="flex aic">
						<text class="fs15 pl5">注册地址</text>
					</view>
					<view class=" "  >
						<text class="fs14  cb pr15">{{city}}</text>
					
					</view>
					<text class=" icon-arrow iconfont fs11 cb pl5"></text>
				</view>
			</wangding-pickerAddress>
		</view>

		<!-- <view class="bgcf pl15 pr15 mt10">
			<view class="fs15 pt15">
				个人简介
			</view>
			<textarea value="" placeholder="请输入您的个人简介…"  class="fs14 pt10"/>
		</view> -->
		<view class="flex  pt25" v-if="tile=='新增企业认证'">
			<view class="funxai flex aic ju_c cf ml15 fs15"  @click='jump' :data-url="'../to_examine/to_examine?id_type='+1+'&id='+1" :data-shifou='true'>
				保存并提交认证
			</view>
			<view class="vaoc flex aic ju_c cf ml20 fs15" @click="bbaoc" >
				保存
			</view>
		</view>
		<view class="" v-else>
			
		</view>
	</view>
</template>

<script>
	import api from '../../components/service.js';
	import yudatetimepicker from '../../components/yu-datetime-picker/yu-datetime-picker.vue'
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		components: {
			yudatetimepicker
		},
		data() {
			const currentDate = this.getDate({
				format: true
			})
			
			return {
				
				shenf: '',
				daib: '',
				daim: '',
				ming: '',
				jgg: '',
				indexg: 0,
				jg: '',
				zijin: '',
				array: ['请选择企业类型', '企业', '自媒体'],
				arrayg: ['请选择公司规模', '200', '500'],
				index: 0,
				city: '请输入注册地址',
				date: '请选择注册时间',
				tile:'',
			}
		},

		onHide() {

		},
		onLoad(option) {
			that = this
			console.log(option)
			if(option.id==1) {
				that.tile='企业信息'
			}else {
				that.tile='新增企业认证'
			}
			uni.setNavigationBarTitle({
			　　title:that.tile
			})
		},
		onShow() {

		},

		computed: {
			startDate() {
				return this.getDate('start');
			},
			endDate() {
				return this.getDate('end');
			},
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			bindDateChange: function(e) {
				this.date = e.target.value
			},
			bindPickerChange: function(e) {
				this.index = e.target.value
				that.jg = this.array[this.index]
				console.log(that.jg)
			},
			bindPickerChangeg: function(e) {
				this.indexg = e.target.value
				that.jgg = this.arrayg[this.indexg]
				console.log(that.jgg)
			},
			change(data) {

				this.city = data.data.join('')
				console.log(data.data.join(''))
				console.log(this.city);
			},
			bindDateChange: function(e) {
				this.date = e.target.value
			},
			getDate(type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();

				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;
				return `${year}-${month}-${day}`;
			},
			bbaoc() {
				uni.showToast({
					title: '保存成功',
					icon: 'none',
				});
				setTimeout(function() {
				wx.navigateBack({
					delta: 1
				})
				}, 1000)
			}
			
		}
	}
</script>

